.verbContainer {
  width: 100%;
  padding-bottom: 80px;

  font-size: 20px;
}

table {
  width: 100%;
  border-collapse: collapse;

  th {
    text-align: center;
    text-transform: capitalize;
    background-color: green;
  }

  tbody>tr>td {
    background-color: rgb(247, 238, 238);
    text-align: left;
    width: 50%;
  }

  tfoot {
    text-align: center;
    align-items: center;
    background: red;
  }
}